






























import Vue, {PropOptions} from 'vue';
import PopUp from '@/configurator/components/utils/PopUp.vue';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import {removeLoadingScreen, showLoadingScreen} from '@/common/utils/dom';
// @ts-ignore
import SvgWarning from '@/common/svgs/warning.svg';
import {isIdAnItem, toBoolean} from '@/common/utils/helper';
import {RapiId} from '@roomle/web-sdk/lib/definitions/typings/rapi-types';
import {GA_CATEGORY} from '@/common/utils/google-analytics';

export default Vue.extend({
  components: {
    PopUp,
    SvgIcon,
  },

  computed: {
    icons(): object {
      return {
        SvgWarning,
      };
    },
  },

  props: {
    payload: String as PropOptions<RapiId>,
    onClose: Function,
  },

  methods: {
    async onChangeAndClose() {
      this.onClose();
      showLoadingScreen();
      const id = this.payload;
      const applyCurrentGlobalParameters = toBoolean(this.$store.state.uiState.initData.applyCurrentGlobalParameters, true);
      const loadParams = {applyCurrentGlobalParameters};
      this.$analytics?.ga?.trackEvent('TypeChangeEvent', GA_CATEGORY.INTERACTION, id);
      if (isIdAnItem(id)) {
        await this.$sdkConnector.loadConfigurableItem(id, loadParams);
      } else {
        await this.$sdkConnector.loadConfiguration(id, loadParams);
      }
      removeLoadingScreen();
    },
  },

});
